import React from 'react';
import AliceCarousel from 'react-alice-carousel';
import '../scss/alice-carousel/alice-carousel.scss';

function Cards() {


    const windWid = window.width;

    const items = [
        {
            "id": 0,
            "imgUrl": "./assets/img/zern.png",
            "title": "Зерновые",
            "text": "Микроэлементы необходимы, так как пшеница заново укореняется вторичной корневой"
        },
        {
            "id": 1,
            "imgUrl": "./assets/img/reps.png",
            "title": "Рапс",
            "text": "Микроэлементы необходимы, так как пшеница заново укореняется вторичной корневой"
        },
        {
            "id": 2,
            "imgUrl": "./assets/img/kukur.png",
            "title": "Кукуруза",
            "text": "Микроэлементы необходимы, так как пшеница заново укореняется вторичной корневой"
        },
        {
            "id": 3,
            "imgUrl": "./assets/img/vegetab.png",
            "title": "Овощи",
            "text": "Микроэлементы необходимы, так как пшеница заново укореняется вторичной корневой"
        },
        {
            "id": 4,
            "imgUrl": "./assets/img/podsoln.png",
            "title": "Подсолнечник",
            "text": "Микроэлементы необходимы, так как пшеница заново укореняется вторичной корневой"
        },
        {
            "id": 5,
            "imgUrl": "./assets/img/bob.png",
            "title": "Бобовые",
            "text": "Микроэлементы необходимы, так как пшеница заново укореняется вторичной корневой"
        }
    ]

    const Gallery = () => {
        const handleOnDragStart = (e) => e.preventDefault()
        return (
          <AliceCarousel 
            responsive={
                {
                    1250: {
                        items: 6
                    },
                    1150: {
                        items: 5
                    },
                    900: {
                        items: 4
                    },
                    600: {
                        items: 3
                    },
                    450: {
                        items: 2
                    },
                    0: {
                        items: 1
                    }
                }
            }
            buttonsDisabled={true}
            autoPlay={true}
            autoPlayInterval={1000}
          mouseTrackingEnabled>
            {items&&
                items.map((el, i) => {
                    return (
                        <div key={i} className="card" onDragStart={handleOnDragStart}>
                            <div className="card__text">
                                <h3>{el.title}</h3>
                                <p>{el.text}</p>
                            </div>
                            <img src={el.imgUrl} alt=""/>
                        </div>
                    )
                })
            }
           
          </AliceCarousel>
        )
      }

    return (
        <section className="cards">
              <div className="cards-content">
                  <div className="cards-content__cards">
                    {Gallery()} 
                  </div>
              </div>
      </section>
    )
}

export default Cards
