import React from 'react'
import logotype from '../assets/img/logo.svg';

function Header() {

    const [open, setOpen] = React.useState(false);

    return (
        <header>
          <div className="wrapper">
              <div className="header-content">
                  <div className="header-content__logo">
                      <img src={logotype} alt="Logo"/>
                  </div>
                  <div className={open? "header-content__nav--open" : "header-content__nav"}>
                      <ul>
                          <li><a href="#">Главная</a></li>
                          <li><a href="#">Новости</a></li>
                          <li><a href="#">Пестициды</a></li>
                          <li><a href="#">Удобрения</a></li>
                          <li><a href="#">Энтомофаги</a></li>
                          <li><a href="#">Контакты</a></li>
                      </ul>
                        <div className={open? "header-content__nav--open-contact" : "header-content__nav-contact"}>
                            <p>info@sopravis.ru</p>
                            <a href="mailto:info@sopravis.ru">Написать на почту</a>
                        </div>
                        <div className={open? "header-content__nav--open-contact" : "header-content__nav-contact"}>
                            <p>+7 (926) 213 9435</p>
                            <a href="tel:+79262139435">Оставить заявку</a>
                        </div>
                  </div>
                  
                 <button  
                     className={open? "hamburger hamburger--spring js-hamburger is-active" : "hamburger hamburger--spring js-hamburger"} 
                     type="button"
                     onClick={() => setOpen(!open)}
                 >
                    <span className="hamburger-box">
                        <span className="hamburger-inner"></span>
                    </span>
                </button>
              </div>
          </div>
      </header>
    )
}

export default Header
